// Цвета
@color-white: #ffffff;
@color-black: #000000;
@color-grin:#68B738;
@color-grin-b:#5EAA2F;
@color-grey-200:#f2f2f2;
@color-grey-600:#E6E6E6;
@color-grey-900: #D9D9D9;
@color-grey-100:#eaeaea;
@color-dark:#444444;

//Шрифты
.lato() {
  font-family:"Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: @color-black;
}
.oswald() {
  font-family: "Oswald",  sans-serif;
  font-style: normal;
  font-weight: 400;
  color: @color-black;
}
